.l-search-page__language-guess, .l-search-page__error {
  margin: 25px 0; }

.l-search-page .product-item-container {
  margin-left: 0;
  margin-right: 0; }

.b-search-page-form > div {
  margin: 5px 0;
  display: inline-block;
  vertical-align: middle; }

@media (min-width: 992px) {
  .b-search-page-form > div {
    margin-right: 10px; } }

.b-search-page-item {
  margin: 30px 0; }
  .b-search-page-item__title {
    color: #202020;
    font-weight: bold; }
    .b-search-page-item__title:hover, .b-search-page-item__title:focus, .b-search-page-item__title:active {
      color: #202020; }
  .b-search-page-item__chain, .b-search-page-item__updated {
    color: #676d72; }
  .b-search-page-item__updated {
    margin-top: 15px; }

.b-search-page-pager {
  text-align: center; }
